body {
  overflow: hidden;
}

main {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.row {
  -ms-overflow-style: none;
  scrollbar-width: none;
  background-color: var(--background-color);
  max-width: 30%;
  flex-wrap: wrap;
  padding: 0 1px;
  display: flex;
  overflow-y: auto;
}

.row::-webkit-scrollbar {
  display: none;
}

.column {
  max-width: 32%;
  flex: 32%;
  padding: 0 .5px;
}

.column img {
  vertical-align: middle;
  width: 100%;
  margin-top: .5px;
}

.column img:hover {
  z-index: 2;
  border-radius: 5%;
  transition: all .5s;
  transform: scale(1.3);
}

.text-container {
  filter: progid:DXImageTransform. Microsoft. gradient(startColorstr= "#000000", endColorstr= "#c2edce", GradientType= 1);
  pointer-events: none;
  z-index: 3;
  background: linear-gradient(90deg, #0000 0%, #c2edce 15% 100%);
  border-radius: 0% 3% 3% 0%;
  margin-left: -15%;
  padding-top: 2%;
}

.text-area {
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 15%;
  padding-left: 3%;
  padding-right: 3%;
  display: flex;
}

.about-heading, .about-intro {
  color: var(--spare-color);
  font-family: Ubuntu, sans-serif;
}

.about-heading {
  text-align: top;
}

.about-intro {
  margin-top: 10px;
}

.back-slide {
  background-color: var(--background-color);
  height: 100%;
  width: 80px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all .4s;
  display: flex;
}

.back-slide:hover {
  width: 160px;
}

.fa-angles-left {
  color: var(--spare-color);
  opacity: .4;
  font-size: 1rem;
  transition: all .4s;
  transform: rotate(180deg);
}

.back-slide:hover .fa-angles-left {
  opacity: 1;
  transform: scale(1.5)rotate(180deg);
}

@media screen and (max-width: 1200px) {
  .column {
    max-width: 49%;
    flex: 49%;
  }

  .fa-circle-arrow-right {
    font-size: 2rem;
  }
}

@media screen and (max-width: 730px) {
  .column {
    max-width: 100%;
    flex: 100%;
  }

  .fa-circle-arrow-right {
    font-size: 1rem;
  }
}

